import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./service-type-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ServiceTypeAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

 
serviceTypeForm: FormGroup;
servicetypeId = 0;  
ajaxData : ServiceTypeData[];
servicetype= new ServiceTypeData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.servicetypeId).subscribe(params=>{
			if(params.servicetypeId != undefined){
				this.servicetypeId=params.servicetypeId; 
			}
		}); 
//	console.log("servicetypeId=="+this.servicetypeId);
	if(this.servicetypeId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.serviceTypeForm = new FormGroup({
			'servicetypeId' : new FormControl(''),
			'servicetypeName' : new FormControl('', [ Validators.required]),
			'servicetypeEntryId' : new FormControl(''),
			'servicetypeEntryDate' : new FormControl(''),
			'servicetypeModifiedId' : new FormControl(''),
			'servicetypeModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get servicetypeName(){ return this.serviceTypeForm.get('servicetypeName')};
	    get servicetypeEntryId(){ return this.serviceTypeForm.get('servicetypeEntryId')};
	    get servicetypeEntryDate(){ return this.serviceTypeForm.get('servicetypeEntryDate')};
	    get servicetypeModifiedId(){ return this.serviceTypeForm.get('servicetypeModifiedId')};
	    get servicetypeModifiedDate(){ return this.serviceTypeForm.get('servicetypeModifiedDate')};
	 
	populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<ServiceTypeData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/contractservicetype-data ',
							 			JSON.stringify({ 'servicetypeId' :this.servicetypeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.servicetype = data['populateData'][0];
																		console.log("servicetypeModifiedId=="+this.servicetype.servicetypeModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }

	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/service-type-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.servicetype[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.serviceTypeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.servicetypeId == 0){ 
				this.servicetype['requestType'] = 'add';
			}else{
				this.servicetype['requestType'] = 'update';
				this.servicetype['servicetypeId'] = this.servicetypeId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/contractservicetype',
									JSON.stringify(this.servicetype),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['errormsg'];
														}
															this.toast.showtoast(this.msg);
															this.toggleAnimation();
															this.serviceTypeForm.reset();
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.servicetype);
	  }
 
  onDelete(){
		 this.servicetype['requestType'] = 'delete';
		this.servicetype['servicetypeId'] = this.servicetypeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/contractservicetype',
									JSON.stringify(this.servicetype),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.serviceTypeForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}
	
export class ServiceTypeData {
    servicetypeId: string;
    servicetypeName: string;
	servicetypeEntryId: string;
	servicetypeEntryBy: string;
	servicetypeEntryDate: string;
	servicetypeModifiedId: string;
	servicetypeModifiedBy: string;
	servicetypeModifiedDate: string;
  
}


